.intro-skills-section {
    background: #fff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    transition: transform 0.1s ease-out;
    perspective: 1000px;
    transform-style: preserve-3d;
    max-width: 800px;
    margin: 2rem auto;
}
  
.introduction {
  margin-bottom: 2rem;
  text-align: left;
  h2 {
    font-size: 2rem;
    margin-bottom: 0.5rem;
    color: #333;
  }

  p {
    font-size: 1rem;
    color: #666;
    line-height: 1.4;
  }
}
  
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  

  .skill {
    display: flex;
    flex-direction: column;
    align-items: center;
    img {
      width: 50px;
      height: 50px;
      object-fit: contain;
      margin-bottom: 0.5rem;
    }
    span {
      font-size: 0.9rem;
      color: #333;
    }
  }
}
  